<template>
  <div class="about">
    <h1>This is an about page</h1>
    <div>{{msg}}</div>
    <div>(num + 1) * 2 = {{result}}</div>
    num: <input v-model="num"/>
    <button @click="plus()">加1</button>
    <div v-if="result>10">result大于10：{{result}}</div>
    <div v-else>result小于等于10：{{result}}</div>
    <div v-show="result>10">result大于10</div>
    <ul v-for="(item, index) in list" :key="item">
      <li v-if="item.classId===1">{{item.no}}：{{item.name}}</li>
      <li v-else-if="item.classId===2">({{item.no}}){{item.name}}</li>
      <li v-else>{{item.no}}：{{item.name}}({{index}})</li>
    </ul>
  </div>
</template>
<script>

export default {
  name: 'HomeView',
  data () {
    return {
      msg: 'Hello World!',
      num: 1,
      list: [{
        name: '张三',
        no: '001',
        classId: 1
      }, {
        name: '李四',
        no: '002',
        classId: 2
      }, {
        name: '王五',
        no: '003',
        classId: 3
      }]
    }
  },
  methods: {
    plus: function () {
      this.num++
    }
  },
  computed: {
    result: function () {
      return ((parseInt(this.num) + 1) * 2)
    }
  },
  watch: {
    num: function (newVal, old) {
      console.log('new:' + newVal + ',old:' + old)
    }
  }
}
</script>
